<template>
    <TopBarVue :datatoggle="data_toggle" :tabs="databs"/>
    <div id="content">
      <router-view />
    </div>
    <Footerbar/>
</template>

<script>
import TopBarVue from "@/layouts/TopBar";
import Footerbar from "@/layouts/Footer.vue"
export default {
  name: "App",
  components: {
    TopBarVue,
    Footerbar
  },
  data(){
    return{
      data_toggle :'',      
      databs:"",
    }
  },
  created(){
    const bdkeylocal = localStorage.getItem("bdkey");
    if (bdkeylocal !== null) {
      this.data_toggle = "";
      this.databs = "";
    }else{
      this.data_toggle = "modal";
      this.databs = "#modalpwd";
    }
  }

};
</script>

